@use "../../foundation/mixins" as *;
@use "../../foundation/variables" as *;

// よくあるご質問
.p-faq {
  &__accordion {
    padding: 5.3rem 0 0 0;
    // 質問
    &-question {
      padding: 2rem 2.8rem 1.7rem 1.9rem;
      border-bottom: 0.05rem solid #000000;
      display: flex;
      align-items: center;
      position: relative;
      background-color: lightblue;
      @include sp {
        padding: initial;
      }
      & span {
        @include sp {
          display: inline-block;
          padding: 1.7rem 0 0.9rem 0;
          font-size: 1.6rem;
          font-weight: 400;
          margin-left: 2rem;
        }
      }
      &::before {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        content: "Q";
        width: 3.5rem;
        height: 3.5rem;
        border-radius: 50%;
        background-color: #000;
        color: $color-white;
        @include sp{
          // margin-right: 2rem;
        }
      }
      &::after {
        position: absolute;
        content: "";
        width: 1.36rem;
        height: 1.36rem;
        border-top: 0.1rem solid #000;
        border-left: 0.1rem solid #000;
        left: 97.5%;
        transform: rotate(45deg);
        transform-origin: left top;
      }
      // 最初の要素のみ上に線を付ける
      &:first-of-type {
        border-top: 0.05rem solid #000000;
      }
      // 文字サイズ
      & span {
        // display: flex;
        // align-items: center;
        // justify-content: center;
        padding-left: 4.1rem;
        font-size: 1.7rem;
        line-height: calc(30 / 17);
      }
    }
    // 回答
    &-answer {
      padding: 2rem 2.8rem 1.7rem 1.9rem;
      border-bottom: 0.05rem solid #000000;
      display: flex;
      align-items: center;
      background-color: lightyellow;
      @include sp {
        padding: initial;
      }
      & span {
        @include sp {
          padding: 1.7rem 4.7rem 0.9rem 5.5rem;
          font-size: 1.6rem;
          font-weight: bold;
        }
      }

      // 文字サイズ
      &::before {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        content: "A";
        width: 3.5rem;
        height: 3.5rem;
        border-radius: 50%;
        background-color: $color-red;
        color: $color-white;
      }
      & span {
        display: inline-block;
        padding-left: 4.1rem;
        font-size: 1.7rem;
        line-height: calc(30 / 17);
      }
    }
  }
}
